import React,{useState,useEffect,useRef} from 'react';
import "./editor.css";
import  axios from 'axios';
import Immutable from 'immutable'
import {EditorState,RichUtils,hasCommandModifier,getDefaultKeyBinding,Modifier,Entity,convertToRaw,AtomicBlockUtils,Media,ContentState,ContentBlock,
  genKey} from 'draft-js'
  import Draft from 'draft-js'
import $ from 'jquery';
import 'draft-js-undo-plugin/lib/plugin.css';
import 'draft-js-image-plugin/lib/plugin.css';
import Editor, { composeDecorators } from 'draft-js-plugins-editor';
import createImagePlugin from "draft-js-image-plugin";
import createStyles from 'draft-js-custom-styles';
import createEntityPropsPlugin from 'draft-js-entity-props-plugin';
import {mediaBlockRenderer} from './mediaBlockRender'
import Draggable from 'react-draggable';
import createAlignmentPlugin from 'draft-js-alignment-plugin';
import createFocusPlugin from 'draft-js-focus-plugin';
import createResizeablePlugin from 'draft-js-resizeable-plugin';
import createBlockDndPlugin from 'draft-js-drag-n-drop-plugin';
import 'draft-js-alignment-plugin/lib/plugin.css';
import createDeleteImgPlugin from 'draft-js-delete-img-btn-plugin';
import createInlineToolbarPlugin from 'draft-js-inline-toolbar-plugin';
import "draft-js-inline-toolbar-plugin/lib/plugin.css";
import createLinkPlugin from 'draft-js-anchor-plugin';
import {
  ItalicButton,
  BoldButton,
  UnderlineButton,
  CodeButton
} from 'draft-js-buttons';
import createLinkifyPlugin from 'draft-js-linkify-plugin';
import createUndoPlugin from 'draft-js-undo-plugin';
import createEmojiPlugin from 'draft-js-emoji-plugin';
import 'draft-js-emoji-plugin/lib/plugin.css';

const emojiPlugin = createEmojiPlugin();
const { EmojiSuggestions, EmojiSelect } = emojiPlugin;

const undoPlugin = createUndoPlugin();
const { UndoButton, RedoButton } = undoPlugin;

const linkifyPlugin = createLinkifyPlugin();
const linkPlugin = createLinkPlugin();
const deleteImgPlugin = createDeleteImgPlugin();
const { DeleteImgBtn } = deleteImgPlugin;
const inlineToolbarPlugin = createInlineToolbarPlugin();
const { InlineToolbar } = inlineToolbarPlugin
const focusPlugin = createFocusPlugin();
const resizeablePlugin = createResizeablePlugin();
const alignmentPlugin = createAlignmentPlugin();
const { AlignmentTool } = alignmentPlugin
const blockDndPlugin = createBlockDndPlugin();
const decorator = composeDecorators(
  resizeablePlugin.decorator,
  alignmentPlugin.decorator,
  focusPlugin.decorator,
   blockDndPlugin.decorator,
   deleteImgPlugin.decorator
);


const imagePlugin = createImagePlugin({ decorator });

const plugins = [
deleteImgPlugin,
 blockDndPlugin,
  focusPlugin,
  alignmentPlugin,
 resizeablePlugin,
  imagePlugin
];

function EditorPannel(argument) {
	// body...
   
   const blockRenderMap = Immutable.Map({
  'textarea': {
    element: 'section'
  }
});
   const extendedBlockRenderMap = Draft.DefaultDraftBlockRenderMap.merge(blockRenderMap);

   const handleTextArea=(event)=>{
    
   }

   const editor = React.useRef(null);
  function focusEditor() {
    editor.current.focus();
  }
  
  const  styleMap=(contentBlock)=> {
  const type = contentBlock.getType();
  if (type === 'code-block') {
    return 'superFancyBlockquote';
  }
  if(type==="atomic"){
    return 'img'
  }
  if(type==="textarea"){
    return 'text-area'
  }
}
 
 const  encodeImageFileAsURL=(element)=> {
  var file = element.target.files[0];
  var reader = new FileReader();
  reader.onloadend = function() {
    const res= insertImage(reader.result)
  setEditorState(res);
  }
  reader.readAsDataURL(file);
}

 const insertImage=(base64)=>{
 	const contentState = editorState.getCurrentContent();

  const contentStateWithEntity = contentState.createEntity(
    "image",
    "IMMUTABLE",
    { src: base64 }
  );
const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
  const newEditorState = EditorState.set(
    editorState,
    { currentContent: contentStateWithEntity },
    "create-entity"
  );
  return AtomicBlockUtils.insertAtomicBlock(newEditorState, entityKey, " ");
 }

	const { styles, customStyleFn, exporter } = createStyles(['font-size', 'color','background-color'],'CHECK_');
    const [selectedStyles,setSelectedStyles]=useState('')
    const [selectedBlockType,setSelectedBlockType]=useState('')
    const [fontSize,setFontSize]=useState()
	const [editorState,setEditorState]=useState(()=>
        EditorState.createEmpty())

	  const handleKeyCommand=(command)=> {

    const newContent = RichUtils.handleKeyCommand(editorState, command);
    if (newContent) {
      setEditorState(newContent); // .. update editor state ...
      return 'handled';
    } else {
      return 'not-handled';
    }
  }
  
    
    const handleCode=(event)=>{
    	event.preventDefault()
    	const res = RichUtils.toggleCode(editorState)
    	setEditorState(res)
    }

	const handleStyleSet=(event)=>{
	event.preventDefault()
	const set=$(event.target).text().toUpperCase();
	const res =RichUtils.toggleInlineStyle(editorState,set);
	setEditorState(res);
	setSelectedStyles(set);
	return("whole");
	}
	const handleBlockType=(event)=>{
		event.preventDefault()
		const res=RichUtils.toggleBlockType(editorState,event.target.id)
		setEditorState(res);
		setSelectedBlockType($(event.target).text())
	}
	const handleBackgroundColor=(event)=>{
		event.preventDefault()
		const res=styles.backgroundColor.toggle(editorState,event.target.value)
		setEditorState(res)
		}
	const handleFontColor=(event)=>{
		event.preventDefault()
          const res = styles.color.toggle(editorState,event.target.value)
          setEditorState(res)
			}
	const handleFontSize=(event)=>{
		event.preventDefault()
		console.log($(event.target).text())
		const res =styles.fontSize.toggle(editorState,$(event.target).text())
		setEditorState(res)
	}
    const handleAlignment=(event)=>{
    	event.preventDefault()
    	console.log(event.target.id)   
    	setAlignText(event.target.id)
    	setAlignmentPlaceholder($(event.target).text())
    	setAlignF(false)
    	 }
    const handleListType=(event)=>{
    	event.preventDefault()
    	const res =RichUtils.toggleBlockType(editorState,event.target.id)
    	setEditorState(res)
    }
    const handleImg=(event)=>{
    	event.preventDefault()
    	$("#img").click()
    }
  const handleBlockQuote=(event)=>{
    event.preventDefault()
    const res = RichUtils.toggleBlockType(editorState,'blockquote')
    setEditorState(res)
  }
  
  const handleFontFamily=(event)=>{
     
     event.preventDefault();
     const res=RichUtils.toggleInlineStyle(editorState,$(event.target).text().toUpperCase())
     setEditorState(res);
  }

	useEffect(()=>{
     axios.get('./family.json')
     .then((result)=>setFamily(result.data.map((item)=>
     	<li style={{paddingBottom:"5px",paddingTop:"5px"}}
     	 className="grey-color"
     	 onMouseDown={handleFontFamily}
     	 >{item.style}</li>)))
	},[])
	const [selectedListType,setSelectedListType]=useState()
	const [alignmentPlaceholder,setAlignmentPlaceholder]=useState()
	const [alignText,setAlignText]=useState('left')
	const [listF,setListF]=useState(false)
	const [alignF,setAlignF]=useState(false)
	const [backgroundColor,setBackgroundColor]=useState('')
    const [colorF,setColorF]=useState(false)
    const [family,setFamily]=useState([])
    const [sflag,setSflag]=useState(false)
    const [flagF,setFlagF]=useState(false)
    const [headingF,setHeadingF]=useState(false)
    const [showFontList,setShowFontList]=useState(false)
    const fontSizes = ['8px', '10px', '12px', '14px', '16px', '18px', '20px', '24px', '28px', '32px', '38px', '46px', '54px', '62px', '72px']
    const fontSizeOptions = fontSizes.map((fontSize) =>
        <li  style={{paddingBottom:"5px",paddingTop:"5px"}}
            className="grey-color"
            onMouseDown={handleFontSize}
        >{fontSize}</li>)

	 return(
	 	<div style={{height:"100%"}}>
	 	<div className="hor-nav">
	 	  <span className="material-icons arrw">
           keyboard_arrow_left
          </span>
          <p className="hor-heading">Editing Project Heading</p>
          <img src="./ironman.png" className="usr-img"/>
          <button type="button" className="upg-btn">UPGRADE</button>
	 	</div>
	 	<div className="ver-nav">
	 	<input type="file" id="img"  onChange={encodeImageFileAsURL} style={{display:"none"}}/>
        <ul className="ver-list">
        <li className="list-item"><i className="material-icons block">view_compact</i>Templates</li>
        <li className="list-item" onMouseDown={handleTextArea}><i className="material-icons block">text_format</i>Text</li>
        <li className="list-item" onMouseDown={handleCode}><i className="material-icons block">code</i>Code</li>
        <li className="list-item" onMouseDown={handleImg}><i className="material-icons block">collections</i>Image</li>
        </ul>
	 	</div>
    
	 	<div className="util-sec">
	 	<div className="wraper">
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" defaultValue={selectedStyles}  size="14" placeholder="Style Text" 
        onMouseDown={(event)=>{
        event.preventDefault()
        setSflag(!sflag)

         }} readOnly/>
        
         {
        	 sflag?<div className="styles">
        	                 <ul style={{padding:"5px"}}>
                     <li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleStyleSet} className="grey-color" >Times_Roman</li>
				             <li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleStyleSet} className="grey-color" ><b>Bold</b></li>
				             <li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleStyleSet} className="grey-color" ><i>Italic</i></li>
				             <li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleStyleSet} className="grey-color" ><u>Underline</u></li>
				             <li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleStyleSet} className="grey-color" ><strike>StrikeThrough</strike></li>
				             </ul>
        	        </div>:null
        }
        </div>
        <div className="wraper">
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" placeholder="Montserrat" size="10" 
        onClick={(event)=>{
        	event.preventDefault()
        	setFlagF(!flagF)
        } }readOnly/>
         {
        	 flagF?<div className="styles">
        	                 <ul style={{padding:"0px",height:"150px",overflow:"scroll",fontSize:"13px",overflowX:"hidden"}} >
        	                 {family}
				             </ul>
        	        </div>:null
        }
        </div>
        <div className="wraper">
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" placeholder="Medium" defaultValue={selectedBlockType} size="5" 
        onMouseDown={(event)=>setHeadingF(!headingF)}
        	 readOnly/>
       {
       	headingF?<div className="styles">
          <ul style={{padding:"0px",height:"150px",overflow:"scroll",fontSize:"12px",overflowX:"hidden",width:"inherit"}}>
          <li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleBlockType} ><h1 id="header-one">H1</h1></li>
          <li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleBlockType} ><h2 id="header-two">H2</h2></li>
          <li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleBlockType} ><h3 id="header-three">H3</h3></li>
          <li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleBlockType} ><h4 id="header-four">H4</h4></li>
          <li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleBlockType} ><h5 id="header-five">H5</h5></li>
          <li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleBlockType} ><h6 id="header-six">H6</h6></li>
          </ul>
        </div>:null
       }
       </div>
        <div className="wraper" style={{width:"0px"}}>
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" style={{padding:"8px"}}  placeholder="25px" size="1.5"
         onMouseDown={(event)=>{
         	event.preventDefault()
         	setShowFontList(!showFontList)}
         } 
         	readOnly/>
        {
        	showFontList?<div className="styles" >
        	<ul style={{padding:"0px",height:"150px",overflow:"scroll",fontSize:"12px",overflowX:"hidden",width:"inherit"}}>
        	{fontSizeOptions}</ul>
        	</div>:null
        }
        </div>


        <div className="wraper">
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" className="material-icons" style={{padding:"6px"}} placeholder="color_lens"  size="1.2"
        onMouseDown={(event)=>{
        	event.preventDefault()
        	setBackgroundColor(!backgroundColor)
        }} />
        {
        	backgroundColor?<div className="styles">
        	<input type="color" defaultValue="black" onChange={handleBackgroundColor} />
        	</div>:null
        }

        </div>


        <div className="wraper">
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" className="material-icons" placeholder="brush" size="1" style={{padding:"6px"}} size="1.5" 
         onMouseDown={(event)=>{
        	event.preventDefault()
        	setColorF(!colorF)
        }}
         readOnly />
         {
         colorF?<div className="styles">
         <input type="color" defaultValue="black" onChange={handleFontColor} />
         </div>:null
        }
        </div>
        <div className="wraper">
        <input list="select" className="material-icons" onMouseDown={handleBlockQuote} placeholder="format_quote" size="1" style={{padding:"6px"}} 
        readOnly />
       
        </div>
        <div className="wraper">
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" className="material-icons" placeholder="format_align_left" defaultValue={alignmentPlaceholder} size="1" style={{padding:"6px"}} 
         onMouseDown={(event)=>{
        	event.preventDefault()
        	setAlignF(!alignF)
        }} readOnly />
         {
        	alignF?<div className="styles ">
        	<ul>
        	<li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleAlignment} className="grey-color"><i className="material-icons" id="center">format_align_justify</i></li>
        	<li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleAlignment} className="grey-color"><i className="material-icons" id="left">format_align_left</i></li>
        	<li style={{paddingBottom:"5px",paddingTop:"5px"}} onMouseDown={handleAlignment} className="grey-color"><i className="material-icons" id="right">format_align_right</i></li>
        	</ul>
        	</div>:null
        }
        </div>
        <div className="wraper">
	    <span className="material-icons down-icon">keyboard_arrow_down</span>
        <input list="select" className="material-icons" defaultValue={selectedListType} placeholder="list" size="1" style={{padding:"6px"}}
        onMouseDown={(event)=>{
        	event.preventDefault()
        	setListF(!listF)
        }}
         readOnly />
       {
         listF?<div className="styles ">
        	<ul>
        	<li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleListType}><i className="material-icons" id="ordered-list-item">format_list_numbered</i></li>
        	<li style={{paddingBottom:"5px",paddingTop:"5px"}} className="grey-color" onMouseDown={handleListType}><i className="material-icons"id="unordered-list-item">format_list_bulleted</i></li>
          	</ul>
        	</div>:null
       } 
        </div>
        </div>
        <div className="undo-btns">
         <UndoButton />
         <RedoButton />
        </div>
        <div className="emoji-sec">
          <EmojiSelect />
        </div>
        <div className="editor-area" id="editor-area" onClick={focusEditor}>
        <Editor 
        ref={editor}
        plugins={plugins,[inlineToolbarPlugin,linkPlugin,linkifyPlugin,undoPlugin,emojiPlugin]}
        blockStyleFn={styleMap}
        customStyleFn={customStyleFn}
        editorState={editorState}
        onChange={setEditorState}
        handleKeyCommand={handleKeyCommand}
        textAlignment={alignText}
        blockRenderMap={extendedBlockRenderMap}
        blockRendererFn={mediaBlockRenderer}
        spellCheck
        autoCorrect="true"/>
        <InlineToolbar >
           {
            (externalProps) => (
              <div>
                <BoldButton {...externalProps} />
                <ItalicButton {...externalProps} />
                <UnderlineButton {...externalProps} />
                <CodeButton {...externalProps}/>
                <linkPlugin.LinkButton {...externalProps} />
              </div>
            )
           }
        </InlineToolbar>
        <EmojiSuggestions />
        <AlignmentTool />
        </div>
	 	</div>
	 	)
}
export default EditorPannel;
